/**
* Cover
*/
.cover {
  max-width: 70%;
  height: 400px;
  z-index: initial;
  margin-right: auto;
  margin-left: auto;
  padding-right: 0px;
  padding-left: 0px;
  @extend %clearfix;
  

  @include media-query($on-laptop) {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
    max-width: calc(#{$content-width} - (#{$spacing-unit}));
    padding-right: $spacing-unit / 2;
    padding-left: $spacing-unit / 2;
  }

  @include media-query($on-palm) {
    height: 400px;
  }
}

.shallow-bg {
  background-image: url($cover-img);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top top;
}

.deep-bg {
  background-image: url($deep-cover-img);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top top;
}

/**
* Profile Namecard Related
*/

.profile-wrapper {
  border-radius: 30px;
  position: relative;
  top: 20%;
  width: 600px;
  // margin-left: auto;
  // margin-right: auto;
  margin: 0 auto;
  @extend %clearfix;

  @include media-query($on-palm) {
    top: 5%;
    border-radius: 25px;
    position: relative;
    width: -webkit-calc(90% - (#{$spacing-unit} / 2));
    width: calc(90% - (#{$spacing-unit} / 2));
    margin-left: auto;
    margin-right: auto;
  }
}

.wrapper-light {
  background-color: rgba(231, 230, 230, .6);
}

.wrapper-dark {
  background-color: rgba(150, 150, 150, .6);
}

.profile-col {
  float: left !important;
  margin-top: $spacing-unit;
  margin-bottom: $spacing-unit;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.profile-col-1 {
  width: -webkit-calc(40% - (#{$spacing-unit} / 2));
  width: calc(40% - (#{$spacing-unit} / 2));
}

.profile-col-2 {
  width: -webkit-calc(60% - (#{$spacing-unit} / 2));
  width: calc(60% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {

  .profile-col-1,
  .profile-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width: calc(50% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .profile-col {
    position: relative;
    margin: 10px auto;
    float: none;
    // width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    // width:         calc(100% - (#{$spacing-unit} / 2));
    width: -webkit-calc(100%);
    width: calc(100%);
  }
}

.circle-img {
  margin: 0px auto;
  width: 168px;
  border-width: 4px;
  border-style: solid;
  border-radius: 50%;
}

.border-dark {
  border-color: $grey-color-dark;
}

.border-bright {
  border-color: $grey-color-bright;
}

.border-light {
  border-color: $grey-color-light;
}

.circle-img-light {
  margin: 0px auto;
  width: 168px;
  border-width: 3px;
  border-style: solid;
  border-radius: 50%;
}

#my-name {
  font-size: 32px;
  font-weight: bold;
  color: $grey-color-dark;
}

#my-title {
  font-size: 20px;
  color: $grey-color-dark;
  font-weight: 200;
}

#my-email {
  font-size: 16px;
  color: $grey-color-dark;
  font-weight: 160;
}

#my-desc-title {
  font-size: 28px;
  color: $grey-color-bright;
  font-weight: bold;
}

#my-desc-content {
  font-size: 18px;
  color: $grey-color-light;
  font-weight: 200;
}

@include media-query($on-palm) {
  #my-title {
    display: none;
  }
}

.preview {
  text-overflow: ellipsis;
  overflow: hidden;
}

.hide {
  display: none;
}